@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #17181a;
  height: 100vh;
  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
  position: relative;
}

main {
  position: absolute;
}

#logo {
  font-family: "Abril Fatface";
  color: white;
}

:root {
  --dark-green-cl: #00853e;
  --light-green-cl: #50c878;
  --primary-cl: #f2726a;
  --black-cl: #242628;
  --white-cl: #ffffff;
}

.nav-header {
  min-height: 10vh;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  width: 80%;
  margin: auto;
  padding: 2rem 0;
  z-index: 20;
  position: relative;
}

.line1,
.line2 {
  width: 3rem;
  height: 0.2rem;
  margin: 0.4rem;
  background-color: white;
  pointer-events: none;
}

.slide {
  position: relative;
  min-height: 100vh;
  margin: 0 auto;
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  background-color: #17181a;
}

.hero-img {
  width: 50%;
  position: relative;
  height: 50vh;
  overflow: hidden;
}
.hero-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  flex-basis: 25rem;
  padding-left: 1.875rem;
  position: relative;
}
.hero-desc h2 {
  font-family: "Abril Fatface";
  font-size: 3.4375rem;
  letter-spacing: 2px;
  font-weight: 500;
}
.hero-desc p {
  padding: 1.25rem 0;
  font-size: 1rem;
  color: #dfdfdf;
}

.mountain-span {
  color: #567ce4;
}

.hike-span {
  color: #a76ee7;
}

.fashion-span {
  color: #ff8b5d;
}

.explore {
  padding: 1rem 2rem;
  border: none;
  background: none;
  color: white;
  border: 1px solid white;
  text-decoration: none;
  font-size: 1rem;
  align-self: flex-start;
  cursor: pointer;
}

.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  z-index: 1;
  opacity: 1;
  clip-path: circle(50px at 100% -10%);
  -webkit-clip-path: circle(50px at 100% -10%);
}

.burger {
  cursor: pointer;
}

.nav-links {
  font-size: 2.5rem;
  padding: 0 12.5rem;
  cursor: pointer;
}
.nav-links h3 {
  padding: 1.25rem 0;
  transition: 0.3s;
}
.nav-links h3:hover {
  color: #567ce4;
}

.contact {
  padding: 0 3.125rem;
}
.contact h2 {
  font-size: 2.5rem;
  padding: 1.25rem 0;
  color: #ff8b5d;
}

.reveal-img,
.reveal-text {
  position: absolute;
  width: 100%;
  height: 150%;
  background-color: #17181a;
  top: -15%;
  left: 0;
}

.cursor {
  width: 50px;
  height: 50px;
  border: 2px solid white;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: all 0.3s ease-in-out;
  transition-property: background, transform;
  transform-origin: 75% 75%;
}

.cursor.nav-active {
  background-color: #567ce4;
  transform: scale(2);
  z-index: 0;
}

.cursor.explore-active {
  background-color: white;
  transform: scale(2.5);
}

.scrollmagic-pin-spacer {
  position: static !important;
}

#logo {
  letter-spacing: 1.5px;
}

.cursor-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0.5rem;
}

.title {
  position: relative;
  overflow: hidden;
}

.title-swipe {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  mix-blend-mode: darken;
  transform: translateY(100%);
}

.t-swipe1 {
  background-color: #567ce4;
}

.t-swipe2 {
  background-color: #a76ee7;
}

.t-swipe3 {
  background-color: #ff8b5d;
}

body.hide {
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  main {
    width: 100%;
    margin: 0;
  }

  .slide {
    display: block;
    width: 100%;
    text-align: center;
    padding: 30px 15px;
  }

  .hero-img {
    height: 60vh;
    width: 100%;
    margin: 0 auto;
  }

  .hero-desc {
    justify-content: center;
    align-items: center;
    padding-left: 0;
  }
  .hero-desc h2 {
    font-size: 40px;
    margin-top: 20px;
  }
  .hero-desc .explore {
    width: 40%;
    margin: auto;
  }

  .nav-bar {
    display: block;
  }

  .nav-links {
    text-align: center;
    margin-top: 100px;
    padding: 0;
  }

  .contact h2 {
    text-align: center;
  }
  .contact P {
    text-align: center;
  }
}
.fashion1,
.fashion2,
.fashion3 {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  color: white;
  margin-top: 50px;
  position: relative;
}

.fashion-img {
  height: 80vh;
}
.fashion-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fashion-text {
  width: 50%;
}
.fashion-text h1 {
  font-size: 3.75rem;
  font-weight: 400;
  padding: 30px 0;
}
.fashion-text p {
  font-size: 1rem;
  padding-right: 20px;
}

.fashion1 h1 {
  color: #ff8b5d;
}

.fashion2 h1 {
  color: #a76ee7;
}

.fashion3 h1 {
  color: #567ce4;
}

.fashion-nr {
  font-size: 200px;
  position: absolute;
  left: 40%;
  bottom: 0;
  color: white;
  opacity: 0.2;
}

.swipe {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.swipe1 {
  background: #a76ee7;
  z-index: 5;
}

.swipe2 {
  background: #567ce4;
  z-index: 4;
}

.swipe3 {
  background: #ff8b5d;
  z-index: 3;
}

@media screen and (max-width: 1024px) {
  .detail-slide {
    width: 100%;
    padding: 0 15px;
  }

  .fashion-nr {
    font-size: 180px;
    left: 30%;
  }
}
@media screen and (max-width: 768px) {
  .detail-slide {
    flex-direction: column;
  }

  .fashion-img {
    height: 68vh;
  }

  .fashion-text {
    text-align: center;
    width: 100%;
  }
  .fashion-text h1 {
    font-size: 50px;
    padding-bottom: 0;
  }
  .fashion-text p {
    padding: 20px 0;
  }

  .fashion-nr {
    left: 70%;
    top: 0;
  }
}